/*
=============== 
Stripe Styles
===============
*/
.sub-nav {
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    position: relative;
    z-index: 1;
}
.sub-nav-center {
    width: 90vw;
    max-width: 1170px;
    max-width: var(--max-width);
}
.sub-nav-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sub-btn {
    font-size: 1rem;
    padding: 0.25rem 0.75rem;
    border-radius: 0.25rem;
    border-radius: var(--radius);
    border-color: transparent;
    color: white;
    background: #222;
    background: var(--clr-black);
    cursor: pointer;
    transition: all 0.3s linear;
    transition: var(--transition);
}
.sub-btn:hover {
    background: hsl(210, 22%, 49%);
    background: var(--clr-grey-5);
}
.sub-nav-links {
    display: none;
}
.sub-signin-btn {
    display: none;
}
.sub-hero::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 65%;
    top: 0;
    left: 0;
    background: url(/website-reactjs/static/media/hero.1382a501.svg);
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
}
.sub-hero {
    position: relative;
    min-height: 100vh;
    margin-top: -5rem;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
}
.sub-hero-center {
    width: 90vw;
    max-width: 1170px;
    max-width: var(--max-width);
    display: grid;
    align-items: center;
}

.sub-hero-info .sub-h1 {
    text-transform: none;
    max-width: 500px;
    margin-bottom: 2rem;
}
.sub-hero-info .sub-p {
    max-width: 35em;
    line-height: 1.8;
}
.sub-hero-images {
    display: none;
}

/* nav media query */
@media screen and (min-width: 800px) {
    .sub-nav-center {
        display: grid;
        grid-template-columns: auto 1fr auto;
        align-items: center;
    }
    .sub-toggle-btn {
        display: none;
    }
    .sub-signin-btn {
        display: inline-block;
    }
    .sub-nav-links {
        display: block;
        justify-self: center;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        text-align: center;
        height: 100%;
        display: grid;
        align-items: center;
    }
    .sub-nav-links li {
        height: 100%;
    }
    .sub-link-btn {
        height: 100%;
        background: transparent;
        border-color: transparent;
        font-size: 1.1rem;
        color: white;
        text-transform: capitalize;
        letter-spacing: 1px;
        width: 10rem;
    }
}

/* hero media query */
@media screen and (min-width: 800px) {
    .sub-hero::before {
        background-size: contain;
        height: 100%;
    }
    .sub-hero-center {
        grid-template-columns: 2fr 1fr;
    }

    .sub-hero-info .sub-h1 {
        font-size: 3rem;
    }
    .sub-hero-info .sub-p {
        font-size: 1.25rem;
    }
    .sub-hero-images {
        display: block;
        justify-self: center;
    }
    .sub-phone-img {
        width: 12rem;
    }
}

